import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
} from '@mui/material';
import React, { FC } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { DRAWER_WIDTH } from './document';

type MdxNode = {
  id?: string;
  frontmatter?: {
    description?: string;
    slug?: string;
    tags?: string[];
    title?: string;
  };
};

const Nav: FC = () => {
  const {
    allMdx: { nodes },
  } = useStaticQuery(graphql`
    query MyQuery {
      allMdx {
        nodes {
          id
          frontmatter {
            slug
            tags
            title
            description
          }
        }
      }
    }
  `);

  return (
    <Drawer
      variant="persistent"
      anchor="left"
      open
      sx={{
        width: DRAWER_WIDTH,
        flexShrink: 0,
        '& .MuiDrawer-paper': {
          width: DRAWER_WIDTH,
          boxSizing: 'border-box',
        },
      }}
    >
      <Toolbar />
      <List>
        {nodes?.map((node: MdxNode) => (
          <ListItemButton key={node.id}>
            <ListItemText primary={node.frontmatter?.title} />
          </ListItemButton>
        ))}
      </List>
    </Drawer>
  );
};

export default Nav;
